.button {
  border: 1px solid black;
  align-self: center;
  align-items: center;
  width: fit-content;
  border-radius: 20px;
  background: #ebebdb;
  font-weight: bold;
  position: relative;
  height: 40px;
  width: 40px;
  text-align: center;
  line-height: 40px;
  margin: 0 10px;
  cursor: pointer;
  letter-spacing: 4px;
  text-indent: 4px;
  font-size: 20px;
}

.button-child {
  position: absolute;
  width: 100%;
  height: 100%;
  border: 1px solid black;
  border-radius: 20px;
  background: #f9dc5c;
  left: -4px;
  top: -3px;
  transition: 0.06s;
  user-select: none;
}

.button-child a {
  text-decoration: none;
  color: #333;
  display: block;
}

.button .button-child:active {
  left: -1px !important;
  top: -1px !important;
  /* border: 0 !important; */
}

.button-active .button-child a {
  color: #fff;
}

.button-active .button-child {
  background-color: #fa4716 !important;
}