/* 第一屏 */
.first_page {
  height: 100%;
  /* background-color: #fb4716; */
}

.first_back_img {
  position: absolute;
  z-index: 0;
}

.first_back_img img {
  min-height: 100%;
  min-width: 100%;
}

/* ----------- */
.content_top {
  display: flex;
  flex-direction: row;
  height: 57.7vh;
  width: 100%;
  border-bottom: 1px dashed #fff;
  position: absolute;
}

.content_top .item:nth-child(1) {
  flex: 1;
  border-right: 1px dashed #fff;
}

.content_top .item:nth-child(2) {
  width: 30vw;
}

.first_page .first_head {
  background-color: #000000;
  height: 100px;
}

.first_content {
  position: absolute;
  height: 100vh;
  width: 100%;
}

.first_content .first_welcome {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -68%);
  width: 88%;
  z-index: 1;
}

.first_content .first_fly {
  position: absolute;
  left: 48.5%;
  top: 48%;
  width: 13%;
  z-index: 0;
}

.first_content .first_ring {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 10%;
  transform: translate(-120%, -180%);
}

.first_content img {
  width: 100%;
}

.content_bottom {
  display: flex;
  flex-direction: row;
  height: 32vh;
  width: 100%;
  position: absolute;
  bottom: 0;
}

.content_bottom .item:nth-child(1) {
  flex: 1;
  display: flex;
  align-items: center;
  border-right: 1px dashed #fff;
}

.first_person_box {
  height: 10vw;
  width: 10vw;
  min-width: 160px;
  min-height: 160px;
  position: relative;
  left: 2vw;
  overflow: hidden;
}

.first_person_box::before {
  content: ' ';
  position: absolute;
  background-image: url('/public/first_text.png');
  background-size: auto 100%;
  background-repeat: no-repeat;
  height: 10vw;
  width: 10vw;
  min-width: 160px;
  min-height: 160px;
  animation: textRotate linear 5s infinite;
}

@keyframes textRotate {
  0% {
    transform: rotate(0deg);
  }

  50% {
    transform: rotate(180deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.first_page .first_person {
  position: relative;
  height: 10vw;
  min-height: 160px;
  animation: person steps(19, end) 4s infinite;
}

@keyframes person {
  100% {
    background-position-x: 100%;
  }
}


.content_bottom .item:nth-child(2) {
  width: 24vw;
  display: flex;
  align-items: center;
}

.first_page .first_arrow {
  position: absolute;
  right: 3vw;
  width: 16vw;
  animation: arrow 4s infinite;
}

@keyframes arrow {
  0% {
    margin-right: 0;
  }

  5% {
    margin-right: -1vw;
  }

  10% {
    margin-right: 0;
  }

  15% {
    margin-right: -1vw;
  }

  20% {
    margin-right: 0;
  }
}

.first_page .first_arrow img {
  width: 100%;
}