/* 第五屏 */
.fifth_page {
  background-color: #ee4425;
  margin-left: 50%;
  height: 100vh;
  width: 150%;
}

.fifth_content {
  display: flex;
  flex-direction: row;
  width: 100vw;
  height: 100vh;
  align-items: center;
  position: relative;
}

.fifth_flex {
  display: flex;
}

.fifth_content .item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.fif_video_box {
  margin: 1vw;
  position: relative;
}

.fif_video_box .video_img {
  width: 80%;
  margin: auto;
}

.fif_img {
  width: 10vw;
  display: block;
  margin: 1vw 0;
}

.video_box {
  position: absolute;
  left: 0;
  top: 0;
  height: 9.5vw;
  width: 15.8vw;
  background-color: gainsboro;
}

/*  */
.join {
  height: 10vw;
  width: 20vw;
  background-color: #fff;
  border-top-left-radius: 10vw;
  border-top-right-radius: 10vw;
  position: absolute;
  bottom: -3vw;
  left: 50%;
  transform: translateX(-50%);
}

.join_link {
  text-align: center;
  border-bottom: 1px solid black;
  width: fit-content;
  margin: auto;
  margin-top: 2.4vw;
  font-weight: bold;
  cursor: pointer;
}

.join_arrow1 {
  position: absolute;
  left: 0;
  bottom: 72%;
  width: 16%;
}

.join_arrow2 {
  position: absolute;
  right: 10%;
  bottom: 86%;
  width: 12%;
}


@media screen and (max-width:812px) {
  .fifth_page {
    margin-left: 0%;
    height: auto;
    width: 100%;
    overflow: hidden;
  }

  .fifth_content {
    height: auto;
  }

  .fifth_flex {
    flex-direction: column;
    margin: auto;
  }

  .fif_img {
    display: none !important;
  }

  .fifth_content .item {
    margin: 40px auto;
    margin-bottom: 100px;
  }

  body {
    overflow-x: hidden;
  }

  .fif_video_box {
    margin: 0;
  }

  .video_box {
    width: 50%;
    height: 44%;
  }

  .fifth_flex .item:nth-child(1) .video_box {
    width: 57%;
  }

  .fifth_flex .item:nth-child(2) .video_box {
    height: 50%;
  }

  .fifth_flex .item:nth-child(3) .video_box {
    height: 46%;
    width: 49.2%;
  }

  .video_img {
    width: 100%;
  }

  .fifth_flex .item:nth-child(3) .video_box {
    left: 24.6vw !important;
  }

  .join {
    bottom: 0;
    margin-bottom: -3vw;
  }
}