.works {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 2000;
  background-color: red;
}

.works_list_box {
  display: flex;
  flex-wrap: wrap;
}

.list_item {
  width: 25%;
  position: relative;
}

@media screen and (min-width:1920px) {
  .list_item {
    width: 20%;
  }
}

@media screen and (min-width:1200px) and (max-width:1919px) {
  .list_item {
    width: 25%;
  }
}

@media screen and (min-width:812px) and (max-width:1200px) {
  .list_item {
    width: 33.3333%;
  }
}

@media screen and (max-width:812px) {
  .list_item {
    width: 50%;
  }
}