.search {
  border-radius: 15px;
  height: 28px;
  border: 1px solid black;
  width: fit-content;
  overflow: hidden;
  background: #f9dc5c;
  display: flex;
}

.search input {
  border: none;
  padding-left: 15px;
  color: #333;
  background: none;
}

.search input:focus-visible {
  border: none !important;
  outline: none;
}

.search .icon {
  cursor: pointer;
  height: 28px;
  width: 28px;
  margin-right: 10px;
}

.search .icon img {
  width: 80%;
  height: 80%;
  margin: 10%;
}