.image {
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.image img {
  border-radius: 5px;
}

.image_title {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.image_time {
  font-size: 14px;
  color: #999;
}

.image_modal {
  height: 100%;
  width: 100%;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 2001;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal_back {
  position: absolute;
  z-index: 0;
  height: 100%;
  width: 100%;
}

.modal_swiper_box {
  text-align: center;
  color: #fff;
  line-height: 30px;
  position: relative;
  height: 50vw;
  width: 90%;
  padding: 40px;
  max-height: 648px;
  max-width: 1152px;
}

.modal_swiper {
  height: 100%;
  width: 100%;
  border-radius: 10px;
  border: 4px solid #fff;
  opacity: visible;
  position: static;
}

.swiper-wrapper {}

.modal_swiper img {
  height: 100%;
  width: 100%;
}

.swiper_control {
  width: 30px;
  overflow: hidden;
}

.swiper_control_left {
  position: absolute;
  left: 0px;
  top: 50%;
  transform: translateY(-50%);
}

.swiper_control_right {
  position: absolute;
  right: -8px;
  top: 50%;
  transform: translateY(-50%);
}

@media screen and (max-width:812px) {
  .swiper_control {
    display: none;
  }
}

.modal_swiper_box span {
  position: absolute;
  display: block;
  width: 90%;
  left: 50%;
  transform: translateX(-50%);
  max-width: 1152px;
  z-index: 1;
}