/* 第二屏 */
.second_page {
  height: 100%;
  background-color: #fb4716;
  position: relative;
  width: 200%
}

.second_container {
  height: 90vw;
  width: 90vw;
  position: absolute;
  left: 50%;
  top: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%);
  background-color: #f9dc5c;
  border-radius: 50%;
}

.second_list_ack {
  position: absolute;
  width: 88%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.second_list_box {
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.second_list_item {
  height: 32vw;
  width: 56vw;
  margin: auto;
  background-color: black;
  transform-origin: 250vw 0;
  position: absolute;
  left: 50%;
  top: 50%;
  cursor: pointer;
  transform: translate(-50%, -50%);
}

.second_list_item img,
.second_list_item a {
  display: block;
  height: 100%;
  width: 100%;
}