.home {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
}


/* 第四屏 */
.fourth_page {
  background-color: #f9dc5c;
  height: 100%;
  position: relative;
  width: 150%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.fourth_item {
  width: 33.333%;
}



@media (max-width:812px) {
  .home {
    display: flex;
    position: absolute;
    flex-direction: column;
    height: auto;
    transform: none !important;
  }

  .second_page {
    width: 100% !important;
  }

  .second_container {
    height: 90vh;
    width: 90vh;
  }

  .fourth_page {
    width: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .fourth_item {
    width: 90%;
    margin: auto;
  }
}